
#app {
  overflow-x: hidden;
  font-family: 'Open Sans', Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}
body {
  height: 100%;
  width: 100%;
  margin: 0;
  background: var(--bg-color);
}


.nav {
  width: 100%;
  z-index: 1000;
  background: var(--nav-bg);
}
.nav ul {
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-pack: start;
        -ms-flex-pack: start;
            justify-content: flex-start;
    list-style-type: none;
    margin: 0;
    padding: 0;
    background-color: var(--nav-bg);
}
@media all and (min-width: 768px) {
.fixed {
    position: fixed;
}
}
@media all and (max-width: 768px) {
.spread-out {
    -webkit-box-orient: vertical;
    -webkit-box-direction: normal;
        -ms-flex-direction: column;
            flex-direction: column;
}
}
.nav li a {
    display: block;
    color: white;
    text-align: center;
    padding: 14px 16px;
    text-decoration: none;
    font-family: var(--header-font);
}
/* Change the link color to #111 (black) on hover */
.nav li a:hover {
    background-color: var(--nav-bg-hover);
}

.sidebar {
  border: 1px solid var(--secondary-color);
  border-radius: 5px;
  padding: 0.25em;
  margin-right: 1em;
}
.sidebar h2 {
  font-family: 'Fjalla One', sans-serif;
  border-bottom: 0.15em solid var(--highlight-color);
}
.sidebar ul {
  padding: 0;
  margin: 0;
  list-style: circle inside url('/static/pin-small.png');
}
.sidebar-elm {
  padding: 0 1em;
  display: block;
  width: 100%;
}
.sidebar:last-child {
  padding-bottom: 1em;
}
@media only screen and (min-width: 768px) {
/* For Desktop view*/
.sidebar {
    position: fixed;
}
}

.overview {
  padding: 0;
}
.overview h1 {
  font-family: var(--heading-font);
  font-size: 4em;
  text-align: center;
}
.overview h2 {
  border-bottom: 0.25em solid var(--highlight-color);
  font-family: var(--heading-font);
  font-size: 3em;
}
.question {
  padding-top: 0.5em;
  font-weight: bold;
  font-size: 1.2em;
  margin-bottom: 1em;
  border-bottom: 0.1em solid var(--highlight-bold-color);
}
.answer {
  text-align: justify;
}























/* Hero Image Classes */
.hero-image {
  width: 100%;
  position:relative;
  display: inline-block;
  float: left;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
}
.hero-image::after {
  content: "";
  clear: left;
  display: block;
}
.hero-image img {
  width: 100%;
  height: auto;
  display:block;
  z-index: -1;
}
.hero-text {
  text-align: center;
  position: absolute;
  top: 50%;
  left: 50%;
  -webkit-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
  font-family: var(--heading-font);
  color: white;
  font-size: 10vw;
  top: 50%;
  text-shadow: 5px 5px black;
}

.video-box {
  padding: 0.5em;
  border: 1px solid var(--secondary-color);
  border-radius: 5px;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
}
.japanese {
  font-family: "M PLUS Rounded 1c";
  color: var(--highlight-bold-dark);
}
video {
  border-radius: 5px;
}
.blog-body {
    padding: 0em 1.5em;
    display: block;
}
.clear {
    display: block;
    clear: left;
    padding-bottom: 1em;
}
.blog-body p {
  text-align: justify;
}

.header {
  padding: 1em 2em;
  border-bottom: 4px solid #ffffff;
}
.header * {
  font-family: var(--heading-font);
  text-align: center;
}
.header h1 {
  font-size: 4em;
}
.blog-post {
  color: var(--primary-color);
}
.blog-post p {
  font-family: var(--body-font);
  font-size: 1.1em;
  word-break: break-none;
}
.blog-post p::before {
  content: "";
  padding: 10px;
}
.blog-post h2 {
  font-family: var(--heading-font);
  font-size: 2em;
  border-bottom: solid 0.25em var(--highlight-color);
}
.blog-post footer {
  padding: 1em 0em;
}
.blog-post footer::before {
  content: "";
  clear: both;
  display: block;
}

.image-card {
    border: 1px solid var(--secondary-color);
    background: var(--bg-color);
    border-radius: 5px;
    padding: 0.25em;
    -webkit-box-shadow: 2px 2px 5px grey;
            box-shadow: 2px 2px 5px grey;
    vertical-align: middle;
    display: block;
}
@media only screen and (min-width: 768px) {
.image-card {
    -webkit-transition: -webkit-transform 0.5s ease-in-out 0.25s;
    transition: -webkit-transform 0.5s ease-in-out 0.25s;
    transition: transform 0.5s ease-in-out 0.25s;
    transition: transform 0.5s ease-in-out 0.25s, -webkit-transform 0.5s ease-in-out 0.25s;
}
.image-card:hover, .image-card:active {
    -webkit-transform: scale(1.5);
            transform: scale(1.5);
    z-index: 1000;
}
}
.image-card img {
  width: 100%;
  height: auto;
  border-radius: 5px;
}
.card-elm {
  display: block;
  width: 100%;
}
.card-caption {
  font-family: var(--heading-font);
  font-size: 0.75em;
  font-style: italic;
  text-align: center;
}


.more-info {
    font-weight: bold;
    font-style: italic;
    position: relative;
    display: inline-block;
    color: var(--highlight-bold-color);
}
.more-info .more-info-text {
    font-weight: normal;
    font-style: normal;
    visibility: hidden;
    width: 400px;
    background-color: var(--primary-color);
    color: var(--secondary-color);
    text-align: center;
    border-radius: 6px;
    padding: 10px;
    position: absolute;
    z-index: 1;
    bottom: 150%;
    left: 50%;
    margin-left: -200px;
}
.more-info .more-info-text::after {
    content: "";
    position: absolute;
    top: 100%;
    left: 50%;
    margin-left: -5px;
    border-width: 5px;
    border-style: solid;
    border-color: black transparent transparent transparent;
}
.more-info:hover .more-info-text {
    visibility: visible;
}
.more-info:active .more-info-text {
    visibility: visible;
}

